<template>
	<div class="container">
		<div v-if="isPageLoading">
			<div class="col-xs-12">
				Loading {{ mName }}'s data
			</div>
		</div>
		<div v-if="!isPageLoading" class="row">
			<div class="col-xs-12">
				<img class="img-fluid" :src="image" />
				<h2><b>{{ name }}</b></h2>
				<p>{{ address }}</p>
				<br>
				<hr>
				<h4 class="text-xs-center font-weight-bold">MENU</h4>
				<hr class="mb-0">
				<div classs="categoryList" v-for="(category, idx) in menu" :key="idx">
					<MenuCategory class="menuCategory" :categoryName='category.name' :items='category.menuList'></MenuCategory>
					<hr class="margin0">
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import MenuCategory from "@/components/MenuCategory";

export default {
  name: "viewMerchant",
  components: {
    MenuCategory
  },
  computed: {
    mID() {
      return this.$route.params.mID;
    },
    mName() {
      return this.$route.params.mName;
    },
  },
  data() {
	  return {
			isPageLoading: false,
			name: 'Thirsty Guys',
			address: 'Faculty of Science S16 NUS',
			image: 'https://via.placeholder.com/1200x500',
			isOpen: true,
			menu: [
				{
					name: 'Food',
					menuList: [
						{
							id: 1,
							name: 'Sunny side egg',
							price: 2
						},
						{
							id: 2,
							name: 'Rice and Beef Brisket',
							price: 12.7
						}
					]
				},
				{
					name: 'Beverages',
					menuList: [
						{
							id: 3,
							name: 'Ice Lemon Kopi',
							price: 3.25
						},
						{
							id: 4,
							name: 'Ice Lemon Tea',
							price: 3.2
						}
					]
				}
			]
	  }
  }
}
</script>

<style scoped>
h2{
	margin-top: 10px;
}
p{
	margin-top: -10px;
}
.margin0{
	margin:0
}
.mb-0{
	margin-bottom: 0;
}
.menuCategory{
	margin-top: 5px;
}
</style>


