<template>
  <nav class="navbar navbar-default navbar-fixed-top">
    <div class="container">
      <router-link class="navbar-brand" :to="{ name: 'home' }">
        STASH
      </router-link>
      <ul v-if="isAuthenticated" class="nav navbar-nav pull-xs-right">
        <li class="nav-item">
          <router-link
            class="nav-link"
            active-class="active"
            exact
            :to="{ name: 'wallet' }"
          >
            <i class="ion-cash"></i>&nbsp; <b>SGD 20</b>
          </router-link>
        <li class="nav-item">
          <router-link
            class="nav-link"
            active-class="active"
            exact
            :to="{ name: 'inbox' }"
          >
            <b><i class="ion-ios-email-outline"></i></b>
          </router-link>
        </li>
        <li class="nav-item" v-if="currentCart.items.length >= 1">
          <router-link
            class="nav-link"
            active-class="active"
            exact
            :to="{ name: 'cart' }"
          >
            <b class="currentCart"><i class="ion-android-cart"></i></b>
          </router-link>
        </li>
        <!-- <li class="nav-item">
          <router-link
            class="nav-link"
            active-class="active"
            exact
            :to="{ name: 'settings' }"
          >
            <i class="ion-gear-a"></i>&nbsp;Settings
          </router-link>
        </li> -->
      </ul>
    </div>
  </nav>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "GeneralHeader",
  computed: {
    ...mapGetters(["isAuthenticated", "currentCart"])
  }
};
</script>

<style scoped>
  .navbar{
    background-color: #ffba00;
    color: white;
    border-color: #e7e7e7;
  }

  .navbar-brand {
    color: white !important;
  }

  li.nav-item{
    background-color: white !important;
    color: #ffba00 !important;
    padding: 0 15px;
    vertical-align: center;
    height: 100%;
    border-radius: 50px;
  }
  
</style>


