<template>
	<div>
    <div v-on:click="toggleCategory()" class="menuCategory">
      <h4 class="text-uppercase"><i v-if="toggledMenu" class="ion-arrow-down-b"></i><i v-if="!toggledMenu" class="ion-arrow-right-b"></i>&nbsp;&nbsp;{{ categoryName }}</h4>
    </div>
    <div class="menuList" v-if="toggledMenu" transition="expand">
      <div class="item" v-for="(item, idx) in items" :key="idx" @click="checkItem(item.id)">
        <div class="row itemContent">
          <div class="col-xs-3">
            <img class="img-fluid" src="https://via.placeholder.com/100" />
          </div>
          <div class="col-xs-6 text-uppercase">
            <div class="font-weight-bold">{{ item.name }}</div>
            <p>
              {{ item.name }}
            </p>
          </div>
          <div class="col-xs-3">
            SGD {{ item.price }}
          </div>
        </div>
        <hr v-if="idx != items.length - 1">
      </div>
    </div>
	</div>
</template>


<script>
import { ADD_TO_CART } from "@/store/mutations.type";

export default {
  name: "MenuCategory",
  props: {
    categoryName: {
      type: String,
      required: true
    },
    items: {
      type: Array,
      required: true
    },
  },
  data () {
    return {
      toggledMenu: false
    }
  },
  methods: {
    toggleCategory () {
      this.toggledMenu = !this.toggledMenu
    },

    checkItem (itemId) {
      console.log(String(itemId))
      this.$store.commit(ADD_TO_CART, String(itemId));
    }
  }
};
</script>

<style scoped>
  .menuCategory{
    padding: 12px 0;
  }
  .menuList{
    margin-top: -10px;
    padding-top: 12px;
    background-color: #f9f9f9;
  }
  .item{
    padding: 10px;
  }
  .itemContent{
    margin-top: -10px;
    padding-bottom: 12px;
  }
  hr{
    margin: 0;
  }
</style>


