<template>
  <div class="home-page">
    <div class="container page">
      <br><h2><b>Food and Beverage</b></h2><br>
      <div class="row justify-content-center">
        <div v-for="(merchant, idx) in merchants" :key="idx"
            class="col-xs-6 col-md-3 itemGrid">
          <router-link
            :to="{ name: 'merchant', params: { 
              mID: merchant.id,
              mName: merchant.name
            } }"
            class="merchantCard"
          >
            <div class="merchantImage">
              <div v-if="!merchant.currentlyOp" class="notOpOverlay">
                <div class="text">Closed</div>
              </div>
              <img class="img-fluid" :src="merchant.image" />
            </div>
            <small>
              <span v-for="(tag, idx2) in merchant.tags" :key="idx2" class="merchantTags">
                {{ tag }}
                <span v-if="idx2 != merchant.tags.length - 1"> - </span>
              </span>
              </small><br>
            <b>{{ merchant.name.toUpperCase() }}</b><br>
            <span>{{ merchant.address }}</span><br>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import { mapState } from "vuex";

export default {
  name: "viewHome",
  data() {
    return {
      merchants: [
        {
          'id': 1,
          'image': 'https://via.placeholder.com/220',
          'name': 'Thirsty Guys',
          'tags': [
            'Food Court',
            'Bubble Tea',
            'Beverages'
          ],
          'address': 'Faculty of Science S16, NUS',
          'distance': 0.67, //in km
          'currentDiscount': 0.67,
          'currentlyOp': true
        },
        {
          'id': 1,
          'image': 'https://via.placeholder.com/220',
          'name': 'Gohan Gohan',
          'tags': [
            'Food Court',
            'Curry',
            'Rice'
          ],
          'address': 'Faculty of Science S16, NUS',
          'distance': 0.67, //in km
          'currentDiscount': 0.67,
          'currentlyOp': false
        },
        {
          'id': 1,
          'image': 'https://via.placeholder.com/220',
          'name': 'Nasi Club',
          'tags': [
            'Food Court',
            'Rice',
            'Lemak'
          ],
          'address': 'Faculty of Science S16, NUS',
          'distance': 0.67, //in km
          'currentDiscount': 0.67,
          'currentlyOp': true
        },
        {
          'id': 1,
          'image': 'https://via.placeholder.com/220',
          'name': 'Serious Salads',
          'tags': [
            'Food Court',
            'Healthy',
            'Salad'
          ],
          'address': 'Faculty of Science S16, NUS',
          'distance': 0.67, //in km
          'currentDiscount': 0.67,
          'currentlyOp': true
        },
        {
          'id': 1,
          'image': 'https://via.placeholder.com/220',
          'name': 'Daily Bowl',
          'tags': [
            'Food Court',
            'Salad',
            'Quinoa'
          ],
          'address': 'Faculty of Science S16, NUS',
          'distance': 0.67, //in km
          'currentDiscount': 0.67,
          'currentlyOp': true
        },
        {
          'id': 1,
          'image': 'https://via.placeholder.com/220',
          'name': 'Test Merchant',
          'tags': [
            'Food Court',
            'Salad',
            'Quinoa'
          ],
          'address': 'Faculty of Science S16, NUS',
          'distance': 0.67, //in km
          'currentDiscount': 0.67,
          'currentlyOp': true
        }
      ]
    };
  },
  computed: {
    ...mapState({
      errors: state => state.auth.errors
    })
  }
};
</script>


<style scoped>
  .itemGrid{
    margin-bottom: 20px;
  }
  
  .merchantTags{
    color: #ffba00;
    padding-bottom: -50px;
  }

  .merchantCard {
    color: black;
  }

  .merchantImage{
    position: relative;
  }

  .notOpOverlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0.7;
    background-color: #777;
  }

  .notOpOverlay > .text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
    font-weight: bold;
    color: black;
  }

</style>


