import ApiService from "@/common/api.service";

import {
  SUBMIT_CART
} from "@/store/actions.type";

import { ADD_TO_CART, REMOVE_FROM_CART, SET_ERROR } from "@/store/mutations.type";

const state = {
  errors: null,
  currentCart: {
    merchant: {

    },
    items: [

    ]
  }
};

const getters = {
  currentCart(state) {
    return state.currentCart;
  },
};

const actions = {
  [SUBMIT_CART](context, credentials) {
    // return new Promise(resolve => {
    //   ApiService.post("users/login", { user: credentials })
    //     .then(({ data }) => {
    //       context.commit(SET_AUTH, data.user);
    //       resolve(data);
    //     })
    //     .catch(({ response }) => {
    //       context.commit(SET_ERROR, response.data.errors);
    //     });
    // });

    return 0;
  },
  
};

const mutations = {
  [SET_ERROR](state, error) {
    state.errors = error;
  },

  [ADD_TO_CART](state, item) {
    state.currentCart.items.push({
      item
    })
    state.errors = {};
  },

  [REMOVE_FROM_CART](state) {
    state.currentCart.items = {};
    state.errors = {};
  }
};

export default {
  state,
  actions,
  mutations,
  getters
};


