<template>
  <div class="container">
    <div class="row">
      <div class="col-xs-12">
        <h4>Hi <span class="text-uppercase font-weight-bold">{{ currentUser.username }}</span>! <small> Your wallet credit balance:</small></h4>
        <br>
        <div class="text-xs-center">
          <h1 class="text-xs-center font-weight-bold">SGD 20</h1>
          <button class="btn btn-primary btn-xl" @click="gotoTopUpWallet">
            <i class="ion-ios-plus-outline"></i><span>&nbsp;Top Up Balance</span>
          </button>
        </div>
        <br>
        <br>
        
        <p>Last transactions:</p>
          <table class="table">
            <thead>
              <tr>
                <th scope="col">Merchant</th>
                <th scope="col">Amount</th>
                <th scope="col">Date Time</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>Mark</td>
                <td>SGD 20</td>
                <td>18 Dec 2019 19:23</td>
              </tr>
              <tr>
                <td>Jacob</td>
                <td>SGD 20</td>
                <td>18 Dec 2019 19:23</td>
              </tr>
              <tr>
                <td>Larry</td>
                <td>SGD 20</td>
                <td>18 Dec 2019 19:23</td>
              </tr>
            </tbody>
          </table>
        <hr>
        <div class="text-xs-center">
          
          <router-link
            class="btn btn-secondary btn-md"
            exact
            :to="{ name: 'settings' }"
          >
            <i class="ion-gear-a"></i>&nbsp;Settings
          </router-link>
          
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "viewWallet",
  data () {
    return {

    }
  },
  computed: {
    ...mapGetters(["currentUser"])
  },
  methods: {
    gotoTopUpWallet () {
      this.$router.push({ name: "walletTopUp"})
    }
  }
};
</script>

<style scoped>
</style>


